code[class*="language-"],
pre[class*="language-"] {
  color: $prism-color;
  background: none;
  text-shadow: $prism-text-shadow;
  font-family: $prism-font-family;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: $prism-line-height;
  tab-size: $prism-tab-size;
  hyphens: none;
  direction: ltr;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background: $prism-background;
}

pre[class*="language-"] {
  padding: $prism-block-padding;
  margin: $prism-block-margin;
  overflow: auto;
  border-radius: $prism-block-border-radius;
}

:not(pre) > code[class*="language-"] {
  padding: $prism-code-padding;
  border-radius: $prism-code-border-radius;
  white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: slategray;
}

.token.punctuation {
  color: #f8f8f2;
}

.namespace {
  opacity: 0.7;
}

.token.property,
.token.tag,
.token.constant,
.token.symbol,
.token.deleted {
  color: #f92672;
}

.token.boolean,
.token.number {
  color: #ae81ff;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #6eba76;
}

.token.entity,
.token.url,
.language-css .token.string,
.style .token.string,
.token.variable {
  color: #f8f8f2;
}

.token.class-name {
  color: #e5c07b;
}

.token.function {
  color: #61afef;
}

.token.atrule,
.token.attr-value {
  color: #e6db74;
}

.token.operator {
  color: #4cb6c2;
}

.token.keyword {
  color: #c678dd;
}

.token.regex,
.token.important {
  color: #fd971f;
}

.token.important,
.token.bold {
  font-weight: bold;
}
.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

.command-line-prompt {
  border-right: 1px solid #999;
  display: block;
  float: left;
  font-size: 100%;
  letter-spacing: -1px;
  margin-right: 1em;
  pointer-events: none;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.command-line-prompt > span:before {
  color: #999;
  content: " ";
  display: block;
  padding-right: 0.8em;
}

.command-line-prompt > span[data-user]:before {
  content: "[" attr(data-user) "@" attr(data-host) "] $";
}

.command-line-prompt > span[data-user="root"]:before {
  content: "[" attr(data-user) "@" attr(data-host) "] #";
}

.command-line-prompt > span[data-prompt]:before {
  content: attr(data-prompt);
}
