@import url(https://rsms.me/inter/inter.css);
.anchor{display:block;float:left;line-height:1.45;margin-left:-30px;padding-right:4px;visibility:hidden}

.rule{border-top:1px solid #DDDDDD;margin-top:24px;margin-bottom:24px}

.section{font-size:16px;padding-top:5em;padding-bottom:5em;line-height:1.45}.section .content{max-width:1200px;margin-left:auto;margin-right:auto;padding-left:2em;padding-right:2em}

/* Examples section */
#examples > .container {
  max-width: 1200px !important;
}

#examples pre[class*="language-"] {
  padding: 0 !important;
}

.demos {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.demos .demo {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
  text-align: left;
  display: flex;
  flex-direction: column;
}

.demos .demo .button {
  margin-top: auto;
}

@media (min-width: 991px) {
  .demos .demo {
    flex: 0 0 33.333333%;
    max-width: 33.333333%;
  }
}

